@media only screen and (max-width: 1000px){
  .sidenav {
		width: 60vw;
	}

	.title-vorlesungen {
		padding-left: 20px;
		padding-right: 40px;
		padding-top: 30px;
		padding-bottom: 20px;
		height: auto;
		color: #e07b8f;
		font-size: 30px;
	}

  #title-img {
		height:100%;
		width:100%;
		filter: opacity(0.4);
		mask-image:none;
		z-index:0;
		object-fit: cover; /* Ensures the image is cropped to fit the container without distortion */
		object-position: center; /* You can adjust this to show the part of the image you want, e.g. top, bottom, etc. */
	}

  .page-titel {	
    display: flex;
    height:80px;
    padding-left:30px;
    color:white;
      position:relative;
      justify-content:left;
      align-items:end;
      justify-items: start;
    font-size: 18px;
    }

    #lecture-subject {
      display: none;
    }

    #lecture-content{
      display: grid; 
      grid-template-columns: 1.1fr 1fr; 
      grid-template-rows: 0.4fr 0.5fr 2.6fr; 
      gap: 30px 30px; 
      grid-template-areas: 
        "Inhalt Inhalt"
        "HotFacts HotFacts"
        "Slides Slides"; 
    }
}

@media only screen and (max-width: 600px){
  .sidenav {
		width: 81vw;
	}
}


